import React, { Fragment, useEffect } from "react";
import "assets/scss/landing.scss";
import logo from "assets/img/logo192.png";
import { NavLink } from "react-router-dom";
import { ArrowDownOutlined } from "@ant-design/icons";
import { useRecoilValue } from "recoil";
import { SESSION_STATE } from "store";

const Landing = () => {
  const session = useRecoilValue(SESSION_STATE);
  useEffect(() => {
    document.title = "Courses | AnC";
    document.body.classList.add("body-banner");
    const landing = document.createElement("script");
    landing.src = "static/js/landing.min.js";
    landing.async = true;
    document.body.appendChild(landing);
    return () => {
      document.body.classList.remove("body-banner");
      document.body.removeChild(landing);
    };
  });
  return (
    <Fragment>
      <ul id="scene">
        <li className="layer" data-depth="0.0" />
        <li className="layer" data-depth="0.10">
          <div className="background" />
        </li>
        <li className="layer" data-depth="0.10">
          <div id="particules">
            <div className="first" id="star" />
          </div>
        </li>
        <li className="layer" data-depth="0.15">
          <div id="particules">
            <div className="second" id="star" />
          </div>
        </li>
        <li className="layer" data-depth="0.20">
          <div className="logo">
            <img src={logo} alt="logo" />
          </div>
        </li>
        <li className="layer" data-depth="0.30">
          <div className="text">
            <p className="sub-title">Courses Reviews</p>
          </div>
        </li>
        <li className="layer" data-depth="0.40">
          <div className="text">
            <h1>Courses</h1>
          </div>
        </li>
        <li className="layer" data-depth="0.42">
          <div id="particules">
            <div className="third" id="star" />
          </div>
        </li>
        <li className="layer" data-depth="0.60">
          <div id="particules">
            <div className="fourth" id="star" />
          </div>
        </li>
      </ul>
      <NavLink to={session.active ? "/search" : "/login"}>
        <ArrowDownOutlined
          style={{
            fontSize: 40,
            position: "absolute",
            bottom: "10vh",
            justifySelf: "center",
            left: 0,
            right: 0,
            margin: "auto",
          }}
        />
      </NavLink>
    </Fragment>
  );
};

export default Landing;
